import React, { PureComponent } from "react";
import { CloudUrl, WebUrl } from "../../../../constants";
import { isSocialWall, isTagEmbed, isTagShop } from "../../../../actions/api";
import { brandingUrl } from "../../../../utils";

function importantCss(el) {
  el.style.setProperty('visibility', 'visible', 'important');
  el.style.setProperty('opacity', '1', 'important');
  el.style.setProperty('display', 'flex', 'important');
  el.style.setProperty('transform', 'none', 'important');
  el.style.setProperty('clip-path', 'initial', 'important');
  el.style.setProperty('border', 'none', 'important');
}

export default class PopUpBranding extends PureComponent {
  render() {
    const { brandingNetworks, languageSetting, wallId, themeID, user } = this.props
    const brandingUrlWithUTM = isTagShop || isTagEmbed ? `${WebUrl}` : `${brandingUrl(brandingNetworks, wallId)[0]}`
    const logoName = isTagEmbed || user == "tagembed" ? 'tagembed-light.svg' : isTagShop ? 'tagshop-light.svg' : isSocialWall ? 'socialwalls-light.svg' : 'tagbox-light.svg'
    return (
      <a href={brandingUrlWithUTM} target={'_blank'}
        ref={(el) => {
          if (el) {
            el.style.setProperty('position', 'absolute', 'important');
            el.style.setProperty('left', 'auto', 'important');
            el.style.setProperty('right', '0', 'important');
            el.style.setProperty('bottom', '-26px', 'important');
            el.style.setProperty('align-items', 'center', 'important');
            el.style.setProperty('padding-top', '4px', 'important');
            el.style.setProperty('display', 'flex', 'important');
            el.style.setProperty('width', 'auto', 'important');
            el.style.setProperty('text-decoration', 'none', 'important');
            el.style.setProperty('direction', 'ltr', 'important');
            importantCss(el)
          }
        }}>
        <div ref={(el) => {
          if (el) {
            el.style.setProperty('font-size', '13px', 'important');
            el.style.setProperty('color', '#fff', 'important');
            el.style.setProperty('margin-right', '5px', 'important');
            el.style.setProperty('width', '100%', 'important');
            el.style.setProperty('height', 'auto', 'important');
            el.style.setProperty('font-family', 'inherit', 'important');
            importantCss(el)
          }
        }}>{Object.keys(languageSetting).length > 0 && languageSetting.poweredBy ? languageSetting.poweredBy : 'Powered by'} </div>
        <div ref={(el) => {
          if (el) {
            el.style.setProperty('background-image', `url(${CloudUrl}${`/media/logo/`}${logoName})`, 'important');
            el.style.setProperty('background-size', 'contain', 'important');
            el.style.setProperty('background-position', 'left center', 'important');
            el.style.setProperty('background-repeat', 'no-repeat', 'important');
            el.style.setProperty('height', '22px', 'important');
            el.style.setProperty('min-height', '22px', 'important');
            el.style.setProperty('width', '90px', 'important');
            el.style.setProperty('min-width', '90px', 'important');
            importantCss(el)
          }
        }}></div>
      </a>
    );
  }
}
