import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { sharePostPopUP, themePostTracking, gtmAnalyticsAction } from '../../../../actions/themeActions'
import { isTagEmbed, isTagShop, isTagbox } from '../../../../actions/api'
import { ensureFullyQualifiedUrl } from '../../../../utils'

class PopUpShare extends PureComponent {

    componentDidMount() {
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
    }

    componentWillReceiveProps(nextProps) {
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
    }


    onShareFacebooklink = (shareURL) => event => {
        window.open(shareURL, "_target")
        const { item, wall } = this.props;
        if (!isTagEmbed) themePostTracking({
            type: 2,
            action: 2,
            wall: wall.Wall.id,
            feed: item.feedId,
            post: item.referenceId ? item.referenceId : item.id,
            owner: wall.Wall.owner
        })
        gtmAnalyticsAction("Tagbox", "popup_share_click", "popup_share", wall.Wall.id)
    }


    onTwitterClick = event => {
        const { item, wall } = this.props;
        if (event) {
            if (!isTagEmbed) themePostTracking({
                type: 2,
                action: 2,
                wall: wall.Wall.id,
                feed: item.feedId,
                post: item.referenceId ? item.referenceId : item.id,
                owner: wall.Wall.owner
            })
            gtmAnalyticsAction("Tagbox", "popup_share_click", "popup_share", wall.Wall.id)
        }
        window.open(item.share.twitter, "_target")
    }

    linkedinShareCount = (shareURL) => event => {
        const { item, wall } = this.props;
        if (!isTagEmbed) themePostTracking({
            type: 2,
            action: 2,
            wall: wall.Wall.id,
            feed: item.feedId,
            post: item.referenceId ? item.referenceId : item.id,
            owner: wall.Wall.owner
        })
        gtmAnalyticsAction("Tagbox", "popup_share_click", "popup_share", wall.Wall.id)
        window.open(shareURL, "_target")
    }

    render() {
        const { sharePostPopUP, item, color } = this.props;
        const fontStyle = {
            // color: color
        };
        return (
            <div className={`tb_post_modal_post_footer_social_${isTagEmbed ? ' tb_post_modal_post_footer_social_te' : ''}${isTagEmbed ? ' tb_post_modal_post_footer_social_ts' : ''}`}>
                <div className="tb_post_modal_share_ico">
                    <div className="tb_post_modal_social_ico_list___">
                        <div onClick={this.onShareFacebooklink(item.share.facebook)}>
                            <div className="tb_post_modal_share_button tb__icon tb-facebook" style={fontStyle}><div></div></div>
                        </div>
                    </div>
                    <div className="tb_post_modal_social_ico_list___" >
                        <div className="tb_post_modal_share_button tb__icon tb-twitter" onClick={this.onTwitterClick} style={fontStyle}><div></div></div>
                    </div>
                    <div className="tb_post_modal_social_ico_list___">
                        <div className="tb_post_modal_share_button tb__icon tb-linkedin" onClick={this.linkedinShareCount(item.share.linkedin)} style={fontStyle}><div></div></div>
                    </div>
                    {isTagbox && item.link ? <div className="tb_post_modal_social_ico_list___" onClick={event => sharePostPopUP(item)}>
                        <div className="tb_post_modal_share_button tb__icon tb-mail" style={fontStyle}> </div>
                    </div> : null}
                    {!isTagEmbed && item.link ? <div className="tb_post_modal_social_ico_list___ tb_post_link__" onClick={event => window.open(item.link, "_blank")}>
                        <div className="tb_post_modal_share_button tb__icon tb-link" style={fontStyle}> </div>
                    </div> : null}
                </div>


                {isTagEmbed && item.link ? <div className="tb_post_modal_view_link" onClick={event => window.open(ensureFullyQualifiedUrl(item.link), "_blank")}>
                    <div className="tb_post_modal_view" style={fontStyle}>{item.network.name}</div>
                    <div className="tb_post_network_ico tb__icon tb-share-square" style={fontStyle}> </div>
                </div> : null}

            </div>
        );
    }
}

const mapStateToProps = state => { }
const mapDispatchToProps = dispatch => {
    return {
        sharePostPopUP: (data) => dispatch(sharePostPopUP(data)),

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(PopUpShare);