import React, { PureComponent, Suspense } from "react";
import { GET_IMAGE_SIZE } from "../../../../utils";
import PostBranding from "../../../Themes/Elements/Branding/PostBranding";
import { isEmbed, isSocialEmbed } from '../../../../actions/api';
const Like = React.lazy(() => import('../../../Themes/Elements/Reactions/Like'));
const PopUpMediaSlider = React.lazy(() => import('./PopUpMediaSlider'));
const PopUpImage = React.lazy(() => import('./PopUpImage'));
const PopUpVideo = React.lazy(() => import('./PopUpVideo'));
const PopUpAudio = React.lazy(() => import('./PopUpAudio'));

export default class PopUpMedia extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      winSize: {
        width: window.innerWidth,
        height: window.innerHeight,
      },
      mediaSize: {
        height: null,
        width: null,
        paddingBottom: null,
      },
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.data !== prevState.data) {
      return {
        ...prevState,
        data: nextProps.data,
      };
    }
    return null;
  }

  componentDidMount() {
    const { data } = this.props;
    window.addEventListener('resize', this.handleResize);
    this.onMediaLoad(data);
  }

  componentDidUpdate(prevProps) {
    if (this.props.data !== prevProps.data) {
      this.onMediaLoad(this.props.data);
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    this.setState({
      winSize: {
        width: window.innerWidth,
        height: window.innerHeight,
      },
    }, () => {
      const { mediaSize } = this.state;
      this.setSize(mediaSize.height, mediaSize.width);
    });
  }

  setSize = (height, width) => {
    const { hideContent } = this.props;
    const { winSize } = this.state;
    const scale = Math.min((winSize.width - 535) / width, (winSize.height - (90 + (hideContent ? 69 : 0))) / height);

    const mediaSize = {
      height: height * scale,
      width: width * scale,
      paddingBottom: (height * 100) / width,
    };

    const allSize = {
      winHeight: winSize.height,
      winWidth: winSize.width,
      height: mediaSize.height,
      width: mediaSize.width,
      paddingBottom: mediaSize.paddingBottom,
    };

    this.props.allSizeCallback(allSize);

    this.setState({
      mediaSize: {
        height: mediaSize.height,
        width: mediaSize.width,
        paddingBottom: mediaSize.paddingBottom,
      },
    });
  }

  onMediaLoad = async (data) => {
    const height = data.mediaHeight ? parseInt(data.mediaHeight, 10) : false;
    const width = data.mediaWidth ? parseInt(data.mediaWidth, 10) : false;

    if (height && width) {
      this.setSize(height, width);
    } else {
      const imageContainer = document.querySelector(`.track${data.id}`);
      const getHeight = imageContainer ? parseInt(imageContainer.getAttribute("data-height"), 10) : 0;
      const getWidth = imageContainer ? parseInt(imageContainer.getAttribute("data-width"), 10) : 0;

      if (getWidth > 0 && getHeight > 0) {
        this.setSize(getHeight, getWidth);
      } else {
        try {
          const { width, height } = await GET_IMAGE_SIZE(data.postFileNew);
          this.setSize(height, width);
        } catch (error) {
          this.setSize(1000, 1000);
        }
      }
    }
  }

  render() {
    const { data, wall } = this.props;
    const { winSize, mediaSize } = this.state;

    const mediaStyle = {
      maxHeight: winSize.width > 991 ? mediaSize.height : null,
      maxWidth: winSize.width > 991 ? mediaSize.width : null,
      aspectRatio: `${mediaSize.width} / ${mediaSize.height}`,
      flexBasis: winSize.width > 991 ? mediaSize.height : null,
    };

    const mediaStyleIn = { paddingBottom: `${mediaSize.paddingBottom}%`, width: winSize.width > 991 ? mediaSize.width : null };

    return (
      <div className="tb_post_modal_media_holder" style={mediaStyle}>
        {data?.postBranding ? <PostBranding size={2} /> : null}
        <div className="tb_post_modal_media" style={mediaStyleIn}>
          <div className="tb_post_modal_media_">
            {isSocialEmbed ? <Suspense fallback={<></>}><Like postId={data.id} wallID={wall.Wall.id} UserDetail={wall.UserDetail} /></Suspense> : null}
            {isEmbed && data.imageList && data.imageList.length > 0 ? (
              <Suspense fallback={<></>}>
                <PopUpMediaSlider imageList={data.imageList} {...this.props} mediaSize={mediaSize} />
              </Suspense>
            ) : data.type === 2 || data.type === 4 ? (
              <Suspense fallback={<></>}>
                <PopUpImage winSize={winSize} ImageUrl={data.filename} {...this.props} mediaSize={mediaSize} opacity={1} hotspot />
              </Suspense>
            ) : data.type === 3 || data.type === 5 ? (
              data.isAudio ? (
                <Suspense fallback={<></>}>
                  <PopUpAudio {...this.props} />
                </Suspense>
              ) : (
                <Suspense fallback={<></>}>
                  <PopUpImage winSize={winSize} ImageUrl={data.filename} {...this.props} mediaSize={mediaSize} opacity={0} />
                  <PopUpVideo {...this.props} key={data.id} />
                </Suspense>
              )
            ) : null}
          </div>
        </div>
      </div>
    );
  }
}
