import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { isSocialWall, isTagEmbed, isTagShop, isTagbox, suiteTrack, isEmbed, isSocialEmbed } from "../../../actions/api";
import PopUpMedia from './Elements/PopUpMedia'
import PopUpAuthor from './Elements/PopUpAuthor'
import PopUpShare from './Elements/PopUpShare'
import PopUpContent from './Elements/PopUpContent'
import PopUpProducts from "./Elements/PopUpProducts";
import { closePopUP, reportMediaPopUp, getDataNextSteps, managePostHeight, themePostTracking } from '../../../actions/themeActions'
import PopUpBranding from "./Elements/PopUpBranding";
import { rID } from "../../../utils";
import './scss/PostPopupSplide.scss'
import './scss/PostPopup.scss'
import CartPopUp from '../CartPopUp'
import MagentoCart from '../CartPopUp/Magento'

class PostPopUp extends PureComponent {
  state = {
    isEnable: 0,
    eventData: {},
    shareText: "SHARE",
    viewOnText: "View on",
    currentIndex: null,
    completeData: {},
    personalization: {},
    url: null,
    imgData: [],
    type: "",
    parentID: "",
    isRepeatingPost: false,
    allSize: {
      winHeight: null,
      winWidth: null,
      height: null,
      width: null,
      paddingBottom: null
    },
    addtoCartPopup: false,
    productImg: '',
    prodImgIdx: 0
  }


  keyboardEvent = (event) => {
    let index = this.state.currentIndex
    if (event.keyCode == 39 || event.keyCode == 40) {
      index += 1;
      this.featuredPopSliderChangeIndex(index)(event)
    } else if (event.keyCode == 37 || event.keyCode == 38) {
      index -= 1;
      this.featuredPopSliderChangeIndex(index)(event)
    } else if (event.keyCode == 27) {
      this.closeWidgetPopUP()

    }
  };


  componentDidMount() {
    const { data } = this.props;
    let p_class = document.querySelector('.tb_app_container')
    let P_id = rID ? rID : p_class?.parentNode?.id
    this.setState({ parentID: P_id })
    this.getWidgetPopUPEvent(data)
    setTimeout(() => {
      var addModal = document.querySelector(`#tb_post_modal_modal-${this.state.parentID}`);
      if (addModal) addModal.classList.add("tb_post_modal_show");

      // if (data.personalization.widgetTheme != undefined && data.personalization.widgetTheme == 47) {
      //   if (addModal) addModal.style.overflow = "hidden";
      // }
    })

    var taggBoxRoot = document.getElementsByTagName('html');
    if (taggBoxRoot && taggBoxRoot[0]) taggBoxRoot[0].style.overflow = "hidden";
    this.popUpCloseClckOnWindow()

    window.addEventListener && window.addEventListener('keydown', this.keyboardEvent);
  }


  componentWillUnmount() {
    window.removeEventListener('keydown', this.keyboardEvent);
  }
  componentDidUpdate() {

    const self = this;
    let modalAction = 0;

    if (window.editor) {
      let modal = document.getElementById("tb_post_modal_modal-taggbox_main");
      let modalBody = document.querySelector(".tb_post_modal_conetent");
      let nextBtn = document.querySelector(".tb_post_modal_nxt_btn");
      let previusButton = document.querySelector(".tb_post_modal_prev_btn");
      if (modal) {
        if (modalBody) { modalBody.onclick = () => modalAction = 1 }
        if (nextBtn) { nextBtn.onclick = () => modalAction = 1 }
        if (previusButton) { previusButton.onclick = () => modalAction = 1 }

        modal.onclick = () => {
          if (modalAction == 0) {
            self.onCloseModal()
          }
          modalAction = 0;
        };
      }
    }
  }


  onCloseModal = () => {
    var addModal = document.querySelector(`#tb_post_modal_modal-${this.state.parentID}`);
    if (addModal && addModal.classList.contains("tb_post_modal_show")) {
      addModal.classList.remove("tb_post_modal_show")
      this.setState({ isEnable: 0, currentIndex: null, type: "", addtoCartPopup: false, selectedProdDetails: null }, this.props.closePopUP());
    }

    var taggBoxRoot = document.getElementsByTagName('html');
    if (taggBoxRoot && taggBoxRoot[0]) {
      taggBoxRoot[0].style.overflow = "";
    }
  }
  closeWidgetPopUP = event => this.onCloseModal()

  getWidgetPopUPEvent = (data) => {
    if (data !== undefined && data != "" && data.card) {
      if (data.type == "post") {
        this.setState({
          url: "",
          isEnable: 1,
          eventData: data,
          currentIndex: data.index,
          completeData: data.idArray,
          viewOnText: data.viewOnText,
          shareText: data.shareText,
          imgData: data.imgData,
          personalization: data.personalization,
          type: data.type
        })
      }
      else if (data.type == "submit-pic") {
        this.setState({
          isEnable: 1,
          type: data.type,
          windowParent: data,
        })
      }
    }

  };
  featuredPopSliderChangeIndex = (currentIndex) => event => {
    const { eventData, isRepeatingPost } = this.state;
    if (((eventData.idArray.length - 1) == currentIndex || currentIndex === eventData.idArray.length) && !isRepeatingPost) this.loadMore()
    else {
      let newIndex;
      if (currentIndex === eventData.idArray.length - 1 || currentIndex === eventData.idArray.length) newIndex = 0;
      else if (currentIndex === -1) newIndex = eventData.idArray.length - 1;
      else newIndex = currentIndex;
      this.setState({
        currentIndex: newIndex,
        isRepeatingPost: false
      }, () => this.onPopUpTrack());
    }
  };
  onPopUpTrack = () => {
    try {
      const { wall } = this.props
      const { currentIndex, completeData, eventData } = this.state

      let item = null;
      if (currentIndex === -1) item = completeData[eventData.idArray.length - 1];
      else item = completeData[currentIndex];
      const requestData = { feed: item.feedId, post: item.referenceId ? item.referenceId : item.id }
      const tagembedPayload = { wallId: wall.Wall.id, userId: wall.Wall.owner, feedId: item.feedId, postId: item.id }
      const ugcPayload = { action: 2, wall: wall.Wall.id, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id, type: 3, owner: wall.Wall.owner }
      themePostTracking(isTagEmbed ? tagembedPayload : suiteTrack ? ugcPayload : { action: 2, wall: wall.Wall.id, ...requestData, owner: wall.Wall.owner })
    }
    catch (error) {
      console.log("Error in PopUp Tracking", error)
    }

  }
  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    let { completeData, eventData } = this.state
    if (completeData.length != postData.postData.length) {

      // let item = completeData[currentIndex]
      let updatePostData = postData.postData.map(pItem => postData.completeDataObject[pItem])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      // let nIndex = findFromArray(filteredPostData, item.id, currentIndex)
      eventData.idArray = filteredPostData
      this.setState({ completeData: filteredPostData, eventData }, () => {
        const { currentIndex } = this.state;
        this.setState({ currentIndex: currentIndex + 1 }, () => this.onPopUpTrack())
      })
    }
    else {
      const hasMoreData = postData.hasMoreData && Object.keys(postData.hasMoreData).length > 0 ? postData.hasMoreData[0] && postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
      if (!hasMoreData) {
        // this.setState({ currentIndex: 0 })
      }
    }



  }
  loadMore = () => {
    const { wall, postData, postHeight } = this.props;
    const { eventData, currentIndex } = this.state;

    const hasMoreData = postData.hasMoreData && Object.keys(postData.hasMoreData).length > 0 ? postData.hasMoreData[0] && postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
    if (eventData.idArray.length - 1 == currentIndex) {
      this.setState({ isRepeatingPost: true });
    }
    if (hasMoreData) {
      const postCount = wall.ThemeRule.numberOfPosts;
      const timeStamp = Math.floor(Date.now() / 1000);
      this.props.managePostHeight(postHeight);
      this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, postData.appendData.networkID, postData.appendData.after, postData.appendData.heightEvent);
    }
    else {
      this.setState({ currentIndex: 0 })
    }

  }
  popUpCloseClckOnWindow = () => {
    document.addEventListener(
      "click",
      (event) => {
        if (event.target.matches(`#tb_post_modal_modal-${this.state.parentID}`)) {
          this.onCloseModal()

        }
      }
    )
  }

  allSize = allSize => {
    this.setState({
      allSize: {
        winHeight: window.innerHeight,
        winWidth: window.innerWidth,
        height: allSize.height,
        width: allSize.width,
        paddingBottom: (allSize.height * 100) / allSize.width
      }
    })
  }

  onClickOpenCart = (id, product, fetchProduct, item) => {
    const { wall } = this.props

    const trackingData = {
      type: 1, action: 2,
      wall: wall.Wall.id,
      feed: item.feedId,
      post: item.referenceId ? item.referenceId : item.id,
      owner: wall.Wall.owner,
      product_id: product.UgcProduct.id,
      currency: product.UgcProduct.price_currency, price: product.UgcProduct.product_discount > 0 ? product.UgcProduct.product_discount : product.UgcProduct.product_price
    }
    this.setState({ addtoCartPopup: true, selectedProdDetails: fetchProduct, tagProdDetail: { ...product, trackingData }, pId: id, productImg: product.UgcProduct.product_image })
  }
  closeCart = event => {
    this.setState({ addtoCartPopup: false, selectedProdDetails: null }, () => {
      var addModal = document.querySelector(`#tb_post_modal_modal-${this.state.parentID}`);
      if (addModal) addModal.classList.add("tb_post_modal_show");
    })
  }
  onUpdateProdImgIdx = (idx) => {
    this.setState({ prodImgIdx: idx })
  }

  render() {
    const { wall, reportMediaPopUp, languageSetting, brandingNetworks, postData } = this.props;
    const { currentIndex, completeData, eventData, parentID, allSize, addtoCartPopup, productImg, selectedProdDetails, tagProdDetail } = this.state;
    let data = null;
    if (currentIndex === -1) data = completeData[eventData.idArray.length - 1];
    else data = completeData[currentIndex];
    const popupSlideShow = (wall.Personalization.popupSlideShow == 1) ? true : false
    const hideContentValue = isTagShop ? wall.ThemeRule.popupContent == 1 ? 0 : 1 : wall.ThemeRule.hideContent
    const hideContent = data && parentID && Object.keys(data).length > 0 && hideContentValue == 1 && data.type != 1 ? true : false
    const isProduct = data && parentID && Object.keys(data).length > 0 && data.ugc_products && data.ugc_products.length > 0 ? true : false
    const socialAction = (wall.ThemeRule.socialAction && isTagShop) || !isTagShop ? true : false

    const tagshopProClass = isTagShop && isProduct ? ' tb_post_modal_ts_p' : ''
    const tagshopClass = isTagShop ? ' tb_post_modal_ts_' : ''
    const tagEmbedClass = isTagEmbed ? ' tb_post_modal_te_' : ''
    const modalClasses = `${tagEmbedClass}${tagshopClass}${tagshopProClass}`

    const tagShopConHeight = { maxHeight: isProduct && isTagShop && allSize.winWidth > 991 ? allSize.height - (socialAction ? 45 : 0) : null }

    return data && parentID && Object.keys(data).length > 0 ? addtoCartPopup ? tagProdDetail?.UgcProduct?.created_from == "magento" ? <MagentoCart productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProdDetails} tagshopProductDeatil={tagProdDetail} closeCart={this.closeCart} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} closePopUp={this.props.closePopUP} /> 
    : 
    <CartPopUp productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProdDetails} tagshopProductDeatil={tagProdDetail} closeCart={this.closeCart} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} closePopUp={this.props.closePopUP} /> 
    : 
    <div className={`tb_post_modal_modal ${!isEmbed ? 'tb_post_wall_modal' : ''}`} id={`tb_post_modal_modal-${parentID}`}>

      <div className={`tb_post_modal_modal_dialog${hideContent && !isProduct ? ' tb_only_media_modal' : ''} ${socialAction && isEmbed ? ' tb_post_modal_modal_full' : ''}${modalClasses}`}>

        <div className="tb_post_modal_conetent">

          {isEmbed ? <div className="tb_post_modal_modal_head">

            <div className="tb_post_modal_modal_header">

              <div className="tb_post_modal_post_link_small">

                <div className="tb_post_modal_post_address">

                  {data.link ? <div className="tb_post_modal_view_link" onClick={event => window.open(data.link, "_blank")}>

                    <div className="tb_post_modal_view">{languageSetting.viewOnText} {data.network.name}</div>

                    <div className="tb_post_view_ico tb__icon tb-arrow-right-alt"> </div>

                  </div> : null}
                </div>
                </div>

                {isEmbed ? <div className="tb_post_modal_close_wrap">
                  <div className="tb_post_modal_close_btn" onClick={this.closeWidgetPopUP}>
                    <div className="tb_post_close_ico tb__icon tb-close-alt"> </div>
                  </div>
                </div> : null}
              </div>
              {isEmbed && popupSlideShow ? <div className={`tb_post_modal_navigation tb_post_modal_nav_content ${data.type === 1 ? 'tb_post_modal_text_nav__' : ''}`}>
                <div className="tb_post_modal_nav tb_post_modal_prev_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex - 1)}>
                  <div className="tb_post_nav tb__icon tb-arrow-left-alt"> </div>
                </div>
                <div className="tb_post_modal_nav tb_post_modal_nxt_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex + 1)}>
                  <div className="tb_post_nav tb__icon tb-arrow-right-alt"> </div>
                </div>
              </div> : null}
            </div> : null}
          <div className="tb_post_modal_body_container">
            <div className="tb_post_modal_modal_body">
              {data.type != 1 ? <div className="tb_post_modal_details_left">
                {isEmbed && popupSlideShow ? <div className="tb_post_modal_navigation tb_post_modal_nav_img">
                  <div className="tb_post_modal_nav tb_post_modal_prev_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex - 1)}>
                    <div className="tb_post_nav tb__icon tb-arrow-left-alt"> </div>
                    </div>
                    <div className="tb_post_modal_nav tb_post_modal_nxt_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex + 1)}>
                      <div className="tb_post_nav tb__icon tb-arrow-right-alt"> </div>
                    </div>
                  </div> : null}
                  <PopUpMedia data={data} wall={wall} allSizeCallback={this.allSize} hideContent={hideContent && !isProduct} />
                </div> : null}
                <div className={`tb_post_modal_details_right${data.type === 1 ? ' tb_post_modal_text__' : ''}`}>
                  <div className={`tb_post_modal_content_wrap`}>
                    <div className="tb_post_modal_content_in" style={tagShopConHeight}>
                      <div className="tb_post_modal_post_detail_wrap">
                        {isProduct ? <PopUpProducts key={`product_${data.id}`} item={data} wall={wall} personalization={wall.Personalization} ugc_products={data.ugc_products} openCart={this.onClickOpenCart} /> : null}
                        <PopUpAuthor key={`author_${data.id}`} item={data} ThemeRule={wall.ThemeRule} Personalization={wall.Personalization} ownerId={wall.Wall.owner} />
                        {!hideContent ? <PopUpContent key={data.id} ThemeRule={wall.ThemeRule} Personalization={wall.Personalization} item={data} contentData={data.content} contentTitle={data.contentTitle} allSize={allSize} isProduct={isProduct} socialAction={socialAction} /> : null}
                      </div>
                    </div>
                    {socialAction && isEmbed ?
                      <div className={`tb_post_modal_post_footer`}>
                        {isTagbox || isSocialWall || isSocialEmbed ?
                          <div className="tb_post_modal_post_footer_report">
                            <div className="tb_post_modal_post_report_media_btn_wrap__" onClick={event => reportMediaPopUp(data)}>
                              <div className="tb_post_modal_post_report_media_btn__">Report</div>
                            </div>
                          </div>
                          : null}
                        {(wall.ThemeRule.socialAction || isTagbox || isTagEmbed) ? <PopUpShare item={data} wall={wall} languageSetting={languageSetting} /> : null}
                      </div>
                      : null}

                  </div>
                </div>
                {(!isSocialWall && !isSocialEmbed && wall?.UserRule?.branding_lite == 1) || postData.isFreeAdsStatus ? <PopUpBranding brandingNetworks={brandingNetworks} languageSetting={languageSetting} wallId={wall.Wall.id} themeID={wall.Personalization.widgetTheme} /> : null}
              </div>
            </div>
          </div>
        </div>
      </div> : null


  }
}
const mapStateToProps = state => {

  return {
            loaderData: state.loaderData,
          postHeight: state.postHeight.data
  }
}
const mapDispatchToProps = dispatch => {
  return {
            closePopUP: (data) => dispatch(closePopUP(data)),
    reportMediaPopUp: (data) => dispatch(reportMediaPopUp(data)),
    managePostHeight: (data) => dispatch(managePostHeight(data)),
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow))

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(PostPopUp);