import React, { PureComponent } from "react";
import ReactHtmlParser from 'react-html-parser';
import Rating from "./PopUpRating";
import { STRING_TO_URL_CONVERT } from '../../../../utils'
import ContentWithOutSlack from '../../../Themes/Elements/PostContent/PostContentConvertion'
import { isSocialWall, isTagShop, isEmbed } from "../../../../actions/api";

export default class PopUpContent extends PureComponent {
  constructor(props) {
    super(props);
    this.upperDivRef = React.createRef();
    this.innerDivRef = React.createRef();
  }
  state = {
    scrollAmount: 1,
    scrollDirection: 1, // 1 = down, -1 = up
    contentHeightInner: 100,
    contentHeightUpper: 100
  };
  // componentDidMount() {
  //   if (isSocialWall && !isEmbed) this.scrollInterval = setInterval(this.scrollContent, 34);
  // }
  // componentWillUnmount() {
  //   if (isSocialWall && !isEmbed) clearInterval(this.scrollInterval);
  // }
  // scrollContent = () => {
  //   try {
  //     const container = this.upperDivRef.current;
  //     const content = this.innerDivRef.current;
  //     const { scrollAmount, scrollDirection } = this.state;
  //     const maxScroll = content.scrollHeight - container.clientHeight;
  //     const currentScroll = container.scrollTop;
  //     if (currentScroll >= maxScroll) this.setState({ scrollDirection: -1 }); // Change direction to up
  //     else if (currentScroll <= 0) this.setState({ scrollDirection: 1 }); // Change direction to down
  //     container.scrollTop += scrollAmount * scrollDirection;
  //   }
  //   catch (e) {
  //     clearInterval(this.scrollInterval);
  //   }
  // };



  componentDidMount() {
    const contentHeightUpEle = this.upperDivRef.current;
    const contentHeightEle = this.innerDivRef.current;
    if (contentHeightEle) {
      this.setState({
        contentHeightInner: contentHeightEle.clientHeight,
        contentHeightUpEle: contentHeightUpEle.clientHeight
      })
    }
  }
  render() {
    const { contentTitle, item, Personalization, ThemeRule, allSize, isProduct, socialAction } = this.props;
    const { contentHeightInner, contentHeightUpEle } = this.state
    const constentHeight = !isProduct && !isTagShop && allSize.winWidth > 991 ? allSize.height - (socialAction ? 100 : 0) : null
    let contentData = item.network.id == 20 ? this.props.contentData : STRING_TO_URL_CONVERT(this.props.contentData);
    if (item.network.id == 12) contentData = ReactHtmlParser(contentData); // for RRS but we have to change from backend.

    const contentDecorateClass = String(this.props.contentData).length > 0 && Personalization.textDecorate ? ' tb_post_modal_text_decorate' : ''

    if (Personalization.widgetTheme === 47) {
      document.querySelector('html').style.overflow = "auto";
    }
    const contentScroll = (!isEmbed && (contentHeightInner > (constentHeight ? constentHeight : contentHeightUpEle))) ? true : false
    return <div className={`tb_post_modal_post_wrapper  ${contentScroll ? `tb_scroll_auto` : ``}`} key={`content_${item.id}`} style={{ height: item.type != 1 ? constentHeight : '100%' }}>
      <div className="tb_post_modal_post_wrap_in" ref={this.upperDivRef}>
        <div className={`tb_post_modal_post_content`}>
          {item.rating != null && item.rating ? <Rating rating={item.rating} network={item.network} /> : null}
          <div className={`tb_post_modal_content tb-cTBfont-${ThemeRule.font_varient} ${contentDecorateClass}`} ref={this.innerDivRef}>
            {contentTitle ? <div className="tb_post_bold_txt">{ReactHtmlParser(contentTitle)}</div> : null}

            <ContentWithOutSlack data={item} content={contentData} Personalization={Personalization} ThemeRule={ThemeRule} />
          </div>
        </div>
      </div>
    </div>
  }
}
