import React, { PureComponent } from "react";
import Network from '../../../Themes/Elements/Network/Network'
import { GET_MEDIA_NAME, timeAgo } from '../../../../utils'
import AuthorAvatar from "../../../Themes/Elements/Author/Elements/AuthorAvatar";
import { isEmbed, isSocialEmbed, isSocialWall, isTagShop } from '../../../../actions/api'
import {
  ensureFullyQualifiedUrl
} from '../../../../utils'

export default class PopUpAuthor extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      picture: props.item.author.picture,
      isAuthorImagevalid: true
    }
  }
  componentWillReceiveProps(nextProps) {
    const { item } = nextProps;
    this.setState({ picture: item.author.picture })
  }
  render() {
    const { item, ThemeRule, ownerId, Personalization } = this.props;

    const authorImage = item.author.picture && String(item.author.picture).includes('author') ? " " : item.author.picture.replace(/[ ]+/g, "")
    const authorUserName = `${item.author.username && item.author.username.length > 0 ? `@` : ``}${item.author.username}`
    const authorDetailStatus = !((isTagShop && !Personalization.postAuthor) || item.postAuthor == 0 || item.author.isInstaUser) ? true : (isSocialWall || isSocialEmbed) ? true : false
    const isDetail = item.timePost == 0 || item.author.isInstaUser
    const dotStatus = isTagShop ? !(!Personalization.postTime || item.postAuthor == 0 || isDetail) ? true : false : !(item.postAuthor == 0 || isDetail) ? true : false
    const postTimeStatus = isTagShop ? !(!Personalization.postTime || isDetail) ? true : false : !(isDetail) ? true : (isSocialWall || isSocialEmbed) ? true : false
    
    return (
      <div className="tb_post_modal_post_author">
        <div className="tb_post_modal_author_details">
          {authorDetailStatus ?
            <div className="tb_post_modal_author_media" style={{ overflow: item.network.id == 4 ? 'visible' : '' }}>
              {!this.state.isAuthorImagevalid ?
                <AuthorAvatar network={item.network} username={item.author.name} authorClass={'tb_post_modal_author_media__'} errorPic={item.author.errorPic} />
                :
                <img loading="lazy" className={'tb_post_modal_author_media__'} src={authorImage && String(authorImage).includes('author.png') ? " " : authorImage.replace(/[ ]+/g, "")} alt={GET_MEDIA_NAME(authorImage)} width={45} height={45}
                  onError={(e) => {
                    this.setState({
                      isAuthorImagevalid: false
                    })
                  }}
                />
              }
            </div>
            : null}

          <div className="tb_post_modal_author_deatils">
            {authorDetailStatus ? <div className="tb_post_modal_author_wrap">
              <div className="tb_post_modal_author_usrname" onClick={event => (item.link && isEmbed) ? window.open(item.link) : null}>{item.author.name} </div>
            </div> : null}
            <div className="tb_post_modal_author_info">
              {authorDetailStatus ? <div className="tb_post_modal_author_handlename">{authorUserName}</div> : null}
              {dotStatus ? <div className="tb_post_modal_seprator_dot"><div></div></div> : null}
              {postTimeStatus ? <span className="tb_post_modal_post_time">{timeAgo(item.createdAt)}</span> : null}
            </div>
          </div>
        </div>
        <div className="tb_post_modal_social_wrapper" onClick={event => (item.link && isEmbed) ? window.open(ensureFullyQualifiedUrl(item.link), "_blank") : null}>
          <Network network={item.network} networkClass={`tb_post_modal_social_ico tb__icon`} font={ThemeRule} ThemeRule={ThemeRule} isDefault={ThemeRule.iconType === 1 ? true : false} isPopUp={true} />
        </div>
      </div >
    );
  }
}
